<template>
  <div
    class="flex-1 ipad:max-h-[255px] ipad:overflow-y-auto ipad:pr-spacing-150"
  >
    <div
      v-for="(agent, index) in agents"
      :key="index"
      class="mb-spacing-300 text-wrap"
    >
      <h3
        v-if="isAgentAvailable(agent)"
        class="mb-spacing-200 text-body/l/relaxed/500 text-content-on-neutral-secondary"
      >
        {{ `Agent ${index + 1} Contact` }}
      </h3>
      <p
        v-if="agent?.genNumber"
        class="neuxs:gap-spacing-100 mb-spacing-150 flex flex-col items-start justify-between text-body/m/relaxed/400 text-content-on-neutral-primary nexus:mb-spacing-0 nexus:flex-row ipad:text-body/l/relaxed/400"
      >
        <span
          class="text-left text-content-on-neutral-secondary nexus:text-content-on-neutral-primary"
          >General Number:</span
        >
        <span class="nexus:text-right">{{ agent?.genNumber }}</span>
      </p>
      <p
        v-if="agent?.commNumber"
        class="neuxs:gap-spacing-100 mb-spacing-150 flex flex-col items-start justify-between text-body/m/relaxed/400 text-content-on-neutral-primary nexus:mb-spacing-0 nexus:flex-row ipad:text-body/l/relaxed/400"
      >
        <span
          class="text-left text-content-on-neutral-secondary nexus:text-content-on-neutral-primary"
          >Commercial Number:</span
        >
        <span class="nexus:text-right">{{ agent?.commNumber }}</span>
      </p>
      <p
        v-if="agent?.email"
        class="neuxs:gap-spacing-100 mb-spacing-150 flex flex-col items-start justify-between text-body/m/relaxed/400 text-content-on-neutral-primary nexus:mb-spacing-0 nexus:flex-row ipad:text-body/l/relaxed/400"
      >
        <span
          class="text-left text-content-on-neutral-secondary nexus:text-content-on-neutral-primary"
          >Email:</span
        >
        <span class="nexus:text-right">{{ agent?.email }}</span>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    company: {
      type: Object,
      required: false,
      default: () => ({}),
    },
  },
  computed: {
    agents() {
      return this.company?.agents || []; // Return an empty array if there are no agents
    },
  },

  methods: {
    isAgentAvailable(agent) {
      // Check if at least one of the fields exists
      return agent?.genNumber || agent?.commNumber || agent?.email;
    },
  },
};
</script>

<style scoped>
/* Add any additional styles here if necessary */
</style>
